.app {
    width: 100%;
    height: 100vh;
    padding: 0;
}

.loginGrid {
    display: grid;
    grid-template-columns: 70% 30%;
}

.loginBg {
    width: 100%;
    height: 100vh;
    background: linear-gradient(90deg, rgba(12, 28, 82, 0.8), rgba(12, 28, 82, 0.8)), url('../../assets/bg1.jpg');
    background-size: cover;
    padding: 10rem;
    color: white;
    padding-top: 20rem;
}

.darkBlue {
    background: rgba(12, 28, 82, 1);
    background-size: contain;
    color: white;
}

.loginBg h1 {
    font-size: 2.5rem;
    font-weight: bold;
}

.loginBg h3 {
    font-size: 2rem;
}

.loginBg h4 {
    font-size: 1.5rem;
    color: goldenrod;
}

.logo {
    width: 100%;
    height: 10rem;
    overflow: hidden;
    text-align: center;
}

.logo img {
    width: 20%;
    margin: 0 auto;
}

.loginContainer {
    width: 100%;
    height: 100vh;
    display: block;
    padding: 10rem 2rem;
    margin: 0 auto;
}

.inputContainer {
    width: 100%;
    height: auto;
    display: block;
    margin-bottom: 1rem;
}

.inputContainer input,
select,
textarea {
    width: 100%;
    height: 5rem;
    padding: .5rem 2rem;
    display: block;
    border: 1px solid gainsboro;
    border-radius: 5px;
    outline: none;
}

.contents {
    width: 87%;
    height: auto;
    display: block;
    margin-left: 13%;
    float: left;

}

.overlay {
    width: 100%;
    height: auto;
    display: block;
    padding: 1rem;
    /* background: linear-gradient(90deg, rgba(57, 204, 149), rgba(58, 205, 198)); */
    background: linear-gradient(90deg, rgba(12, 28, 82, 0.8), rgba(12, 28, 82, 0.8)), url('../../assets/bg1.jpg');
    background-size: cover;
    background-position: center;
    color: white;

}

.cardContainer {
    width: 100%;
    height: auto;
}

.cardGrid {
    display: grid;
    grid-template-columns: 23% 23% 23% 23%;
    grid-gap: 2%;
}

.purple {
    background: #F2EBFB;
    color: #713BDB;
}

.cyan {
    background: #EEF9FF;
    color: #49B8FF;
}

.gold {
    background: #FEF3EF;
    color: #FE8A57;
}

.red {
    background: #FFEFF2;
    color: #FD5181;
}

.title {
    width: 100%;
    height: 3rem;
    color: white;
}

.guestContainer {
    width: 100%;
    height: auto;
    display: block;
    padding: 5rem;
}

.guestGrid {
    display: grid;
    grid-template-columns: 30% 70%;
}

.guestCard {
    width: 100%;
    height: auto;
    display: block;
    padding: 2rem;
}

.guestCard h2 {
    font-size: 15px;
}


.titleCard {
    width: 100%;
    height: 3rem;
    display: block;
    padding: 1rem .5rem;
    background: white;
    margin-bottom: 1rem;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    border-radius: 5px;
}

.guestCard p {
    font-size: 14px;
}

.guestCard img {
    border-radius: 50%;
}

.whiteBtn {
    padding: 1rem 2rem;
    background: whitesmoke;
    color: black;
    font-size: 12px;
    border-radius: 5px;

}

.rows {
    display: grid;
    grid-template-columns: 48% 48%;
    grid-gap: 2%;
}

.rows input {
    padding: 1rem 2rem;
    border: 1px solid gainsboro;
}

.rows select {
    padding: 1rem 2rem;
    border: 1px solid gainsboro;
}