.app {
    width: 100%;
    min-height: 100vh;
    height: auto;
    display: block;
    background: white;
    background-size: cover;
}

.circle {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 1px solid gainsboro;
    text-align: center;
    margin: 0 auto;
}

.circle img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
}

.box {
    width: 100%;
    height: auto;
    display: block;
    padding: 1rem 5rem;
    text-align: center;
}

.btnContainer {
    width: 50%;
    height: 7rem;
    margin-bottom: 1rem;
    margin: 0 auto;

}

.btn {
    height: 6rem;
    padding: 1.8rem 3rem;
    border-radius: 5px;
    transition: all 0.2s linear;
    text-align: center;
    font-size: 1.5rem;
    display: block;
}

.green {
    background: green;
    color: white;
}

.gold {
    background: blue;
    color: white;
}


.red {
    background: red;
    color: white;
}


.black {
    background: goldenrod;
    color: white;
}


/* Forms */
.formPadding {
    width: 100%;
    height: auto;
    display: block;
    padding: 3rem 30rem;
    color: black;
}

.inputContainer {
    width: 100%;
    height: auto;
    display: block;
    margin-bottom: 1rem;
}

.inputContainer input,
select,
textarea {
    width: 100%;
    height: 5rem;
    padding: .5rem 2rem;
    display: block;
    border: 1px solid gainsboro;
    border-radius: 5px;
    outline: none;
}

.inputContainer input:hover {
    outline: none;
}

.left {
    float: left;
}

.right {
    float: right;
}

.white {
    background: gainsboro;
    color: black;
}

a svg {
    float: left;
    margin-right: 1rem;
    font-size: 1.5rem;
    margin-top: .2rem;
}

button svg {
    float: left;
    margin-right: 1rem;
    font-size: 1.5rem;
    margin-top: .2rem;
}


.customBtn {
    padding: 1rem 4rem;
    border-radius: 50px;
    text-decoration: none;
    display: block;
    text-align: center;
    transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
    font-size: 1.5rem;
}

.customBtn:hover {
    transform: scale(1.1);
}

.formPadding h3 {
    font-size: 2rem;
    color: black;
}

.accordion {
    height: 4rem;
    border: 1px solid gainsboro;
    color: black;
    font-size: 1.3rem;
    margin-bottom: 1rem;
    border-radius: 50px;
}

.accordion:hover {
    background: #28a745;
    color: white;
}

.dangerBtn {
    padding: 1rem 4rem;
    border-radius: 50px;
    text-decoration: none;
    font-size: 1rem;
    background: crimson;
    display: inline-block;
    color: white;
}